declare const cv: any;

export function extractBlurCoefficient(canvas: any): number {
  const src = cv.imread(canvas);
  const dst = new cv.Mat();
  const men = new cv.Mat();
  const menO = new cv.Mat();
  cv.cvtColor(src, src, cv.COLOR_RGB2GRAY, 0);

  // You can try more different parameters
  const t = cv.Laplacian(src, dst, cv.CV_64F, 1, 1, 0, cv.BORDER_DEFAULT);
  cv.meanStdDev(dst, menO, men);
  const coef = men.data64F[0];

  src.delete();
  dst.delete();
  men.delete();
  menO.delete();

  return coef;
}

export function extractImageFromPrediction(
  prediction: any,
  src: any,
  scale: boolean
): any {
  const rect = new cv.Rect(
    prediction.bbox[0],
    prediction.bbox[1],
    prediction.bbox[2],
    prediction.bbox[3]
  );
  const dst = src.roi(rect);
  const dstt = new cv.Mat();

  cv.copyMakeBorder(dst, dstt, 10, 10, 10, 10, cv.BORDER_CONSTANT);
  const dstt_ = new cv.Mat();

  cv.copyMakeBorder(dstt, dstt_, 100, 100, 100, 100, cv.BORDER_ISOLATED);
  const dstt__ = new cv.Mat();

  console.log("before resize: ", dstt_.matSize);
  const scaleFactor = scale ? 0.6 : 0.85;
  // const scaleFactor = 1.0

  cv.resize(
    dstt_,
    dstt__,
    dstt__.size(),
    scaleFactor,
    scaleFactor,
    cv.INTER_AREA
  );

  dst.delete();
  dstt.delete();
  dstt_.delete();

  return dstt__;
}

// helper function -> converts String base64 data to BLOB
export function makeblob(
  b64Data: string,
  contentType?: string,
  sliceSize?: number
) {
  contentType = contentType || "";
  sliceSize = sliceSize || 512;

  const byteCharacters = atob(b64Data);
  const byteArrays = [];

  for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
    const slice = byteCharacters.slice(offset, offset + sliceSize);

    const byteNumbers = new Array(slice.length);
    for (let i = 0; i < slice.length; i++) {
      byteNumbers[i] = slice.charCodeAt(i);
    }

    const byteArray = new Uint8Array(byteNumbers);

    byteArrays.push(byteArray);
  }

  const blob = new Blob(byteArrays, { type: contentType });
  return blob;
}
